import Cookies from 'universal-cookie';


/* Auth
 ============================================================================= */

function storeAuthCookie(store, username, user_key){
    const cookies = new Cookies();
    // set cookies for 2 days
    cookies.set('username', username, {path: '/', maxAge: 60 * 60 * 24 * 2});
    cookies.set('user_key', user_key, {path: '/', maxAge: 60 * 60 * 24 * 2});
    store.user_key = user_key;
    store.username = username;
}

// used on failed validation and logout
export function clearAuth(store){
    const cookies = new Cookies();
    cookies.remove('username');
    cookies.remove('user_key');
    store.user_key = null;
    store.username = null;
    window.socket.emit('logout', true);
}

// used to authenticate cookie token if it exists
export function cookieLogin(store){
    const cookies = new Cookies();
    if(cookies.get('user_key') && cookies.get('username')){
        store.isAuthenticating = true;
        console.log('Validating Cookies');
        const authObj = {
            username: cookies.get('username'),
            user_key: cookies.get('user_key')
        };
        window.socket.emit('keyAuth', authObj, response =>{
            if(response.isAuth){
                storeAuthCookie(store, response.username, response.user_key);
                console.log('Cookies validated');
            } else {
                clearAuth(store);
                console.log('Cookies invalid');
            }
            store.isAuthenticating = false;
        });
    } else {
        console.log('No Cookies found');
    }
}

// used to authenticate login
export function passwordLogin(store, username, password, callback){
    store.isAuthenticating = true;
    console.log('Validating Login');
    const authObj = {
        username: username,
        password: password
    };
    window.socket.emit('passwordAuth', authObj, response =>{
        if(response.isAuth){
            storeAuthCookie(store, response.username, response.user_key);
            console.log('Login success');
            callback(true);
        } else {
            clearAuth(store);
            console.log('Login Fail');
            callback(false)
        }
        store.isAuthenticating = false;
    });
}


