import React, {Component} from 'react';
import {observer} from 'mobx-react';
import PageTitle from "../components/PageTitle";

class PoolRow extends Component {
    constructor(props){
        super(props);
        this.linkClick = this.linkClick.bind(this);
    }
    linkClick(){
        const hasLink = this.props.pool.link && this.props.pool.link !== '';
        if(hasLink){
            window.open(this.props.pool.link, "_blank")
        }
    }
    render(){
        const pool = this.props.pool;
        const img = `/img/${pool.coin_ticker}.png`;
        const hasLink = pool.link && pool.link !== '';
        let rowClass = this.props.current ? 'pool-row current-pool' : 'pool-row';
        rowClass += pool.active ? '' : ' disabled';
        rowClass += hasLink ? ' hasLink' : '';
        return(
            <tr className={rowClass}>
                <td className="imgcell" onClick={()=>{this.linkClick()}}>
                    <img src={img}/>
                </td>
                <td onClick={()=>{this.linkClick()}}>
                    {pool.coin_ticker}
                    <div className="small"> {pool.coin_name}</div>
                </td>
                <td onClick={()=>{this.linkClick()}}>{pool.algo_name}</td>
                <td onClick={()=>{this.linkClick()}}>{pool.name}</td>
                <td onClick={()=>{this.linkClick()}}>{pool.hostname}:{pool.port}</td>
                <td onClick={()=>{this.linkClick()}}>{pool.username}</td>
                <td onClick={()=>{this.linkClick()}}>{pool.password}</td>
                <td className="tool-cell">
                    <div className="tool-btn"
                         title="Edit"
                         onClick={()=>{this.props.edit(pool.id)}}>
                        <i className="fa fa-edit" style={{color:'#3e7ebd'}}></i>
                    </div>
                    <div className="tool-btn"
                         title="Delete"
                         onClick={()=>{this.props.delete(pool.id)}}>
                        <i className="fa fa-close" style={{color:'#da3d25'}}></i>
                    </div>
                    {!this.props.current ?
                        <div className="tool-btn"
                             title="Make Current"
                             onClick={()=>{this.props.makeCurrent(pool.id)}}>
                            <i className="fa fa-arrow-up" style={{color:'#5fb23e'}}></i>
                        </div>
                    :
                    null}
                </td>
            </tr>
        )
    }
}

@observer
class Pools extends Component {
    constructor(props) {
        super(props);
        this.state = {
            popup: false,
            popupTitle:null,
            poolId:0,
            poolName:'',
            poolCoin:0,
            poolAlgo:0,
            poolUrl:'',
            poolPort:'',
            poolUsername:'',
            poolPassword:''
        };

        this.handleInputChange = this.handleInputChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.editPoolPopup = this.editPoolPopup.bind(this);
        this.makeCurrent = this.makeCurrent.bind(this);
        this.deletePool = this.deletePool.bind(this);
    }
    handleInputChange(event) {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        this.setState({
            [name]: value
        });

        // change algo to coin
        if(name === 'poolCoin'){
            if(parseInt(value) === 0){
                this.setState({
                    poolAlgo:0
                })
            } else {
                // get coin id
                let coins = this.props.store.coins;
                let algoId = null;
                for(let ticker in coins){
                    if(coins[ticker].id === parseInt(value)){
                        algoId = coins[ticker].algoId;
                        break;
                    }
                }
                this.setState({
                    poolAlgo:algoId
                })
            }
        }
    }

    handleSubmit(event) {
        event.preventDefault();
        console.log(`Adding pool ${this.state.poolName}`);
        const poolObj = {
            id:parseInt(this.state.poolId),
            name:this.state.poolName,
            coin_id:parseInt(this.state.poolCoin),
            algo_id:parseInt(this.state.poolAlgo),
            hostname:this.state.poolUrl,
            port:parseInt(this.state.poolPort),
            username:this.state.poolUsername,
            password:this.state.poolPassword,
            active:parseInt(this.state.poolActive),
            link:this.state.poolLink
        };
        window.socket.emit('addEditPool', poolObj, response =>{
            this.props.store.pools = response;
        });
        this.closePopup();
    }

    addPoolPopup() {
        this.setState({
            popup: true,
            popupTitle:'Add Pool',
            poolId:0,
            poolName:'',
            poolCoin:0,
            poolAlgo:0,
            poolUrl:'',
            poolPort:'',
            poolUsername:'',
            poolPassword:'',
            poolLink:'',
            poolActive:1
        })
    }

    editPoolPopup(id) {
        // get pool info
        let pools = this.props.store.pools;
        let pool = null;
        for(let i = 0; i < pools.length; i++){
            if(pools[i].id === id){
                pool = pools[i];
                break;
            }
        }
        this.setState({
            popup: true,
            popupTitle:`Edit Pool: ${pool.name}`,
            poolId:id,
            poolName:pool.name,
            poolCoin:pool.coin_id,
            poolAlgo:pool.algo_id,
            poolUrl:pool.hostname,
            poolPort:pool.port,
            poolUsername:pool.username,
            poolPassword:pool.password,
            poolLink:pool.link || '',
            poolActive:pool.active
        })
    }

    deletePool(id){
        // todo add confirmation
        console.log(`Deleting pool ${id}`);
        window.socket.emit('deletePool', id, response =>{
            this.props.store.pools = response;
        });
    }
    makeCurrent(id){
        // todo add confirmation
        console.log(`Making pool current ${id}`);
        window.socket.emit('makePoolCurrent', id, response =>{
            this.props.store.pools = response;
        });
    }

    closePopup() {
        this.setState({
            popup: false
        })
    }

    verifyPoolPopup() {
        const encode = (key) =>{return encodeURIComponent(this.state[key]);};
        // get nicehash id for algo
        const algos = this.props.store.algos;
        let nicehashAlgoId = 0;
        for(let i = 0; i < algos.length; i++){
            if(algos[i].id === parseInt(this.state.poolAlgo)){
                nicehashAlgoId = this.props.store.algos[i].nicehash_id;
            }
        }
        // console.log(`nice hash algo id ${nicehashAlgoId}`);
        const url = `https://api.nicehash.com/poolver.jsp?&&a=${nicehashAlgoId}&poolhost=${encode('poolUrl')}&poolport=${encode('poolPort')}&pooluser=${encode('poolUsername')}&poolpass=${encode('poolPassword')}`;
        const newWindow = window.open(url,'name','height=650,width=770');
        if (window.focus) {
            newWindow.focus()
        }
    }

    render() {
        // check to see if access cookie exists.
        let algorithmOptions = [<option value={0} key={-1}></option>];
        const algos = this.props.store.algos;
        if(algos){
            for(let i = 0; i < algos.length; i++){
                algorithmOptions.push(<option value={algos[i].id} key={i}>{algos[i].name}</option>)
            }
        }

        let coinOptions = [<option value={0} key={-1}></option>];
        const coins = this.props.store.coins;
        if(coins){
            for(let ticker in coins){
                coinOptions.push(<option value={coins[ticker].id} key={ticker}>{ticker}</option>)
            }
        }

        let poolRowsCurrent = [];
        let poolRows = [];
        let poolRowsDisabled = [];
        const pools = this.props.store.pools;
        // first do current pool rows
        if(pools){
            for(let i = 0; i < pools.length; i++){
                if(pools[i].current === 1 && pools[i].active === 1){
                    poolRowsCurrent.push(<PoolRow current={true} pool={pools[i]} key={i} edit={this.editPoolPopup} delete={this.deletePool} makeCurrent={this.makeCurrent}/>)
                } else if (pools[i].active === 1) {
                    poolRows.push(<PoolRow current={false} pool={pools[i]} key={i} edit={this.editPoolPopup} delete={this.deletePool} makeCurrent={this.makeCurrent}/>)
                } else {
                    poolRowsDisabled.push(<PoolRow current={false} pool={pools[i]} key={i} edit={this.editPoolPopup} delete={this.deletePool} makeCurrent={this.makeCurrent}/>)
                }
            }
        }
        return (
            <div className="container-fluid content">
                <PageTitle text="Pools" store={this.props.store}/>
                <div className="row" style={{marginBottom: '20px'}}>
                    <div className="col-xs-6" style={{fontSize: '24px'}}>
                        My Pools
                    </div>
                    <div className="col-xs-6 text-right">
                        <button className="btn btn-warning"
                                onClick={this.addPoolPopup.bind(this)}>
                            <i className="fa fa-plus"></i> Add Pool
                        </button>
                    </div>
                </div>
                <div className="panel panel-default">
                    <div className="panel-body">
                        {this.props.store.pools ?
                            <table className="table table-striped compact" style={{marginBottom: '5px'}}>
                                <thead>
                                <tr>
                                    <th className="imgcell"></th>
                                    <th>Coin</th>
                                    <th>Algo</th>
                                    <th>Pool Name</th>
                                    <th>Hostname</th>
                                    <th>Username</th>
                                    <th>Password</th>
                                    <th className="tool-cell">Tools</th>
                                </tr>
                                </thead>
                                <tbody>
                                {poolRowsCurrent}
                                {poolRows}
                                {poolRowsDisabled}
                                </tbody>
                            </table>
                            :
                            <div className="text-center">
                                <h4>You have no pools</h4>
                                <button className="btn btn-warning"
                                        onClick={this.addPoolPopup.bind(this)}>
                                    <i className="fa fa-plus"></i> Add Pool
                                </button>
                            </div>
                        }
                    </div>
                </div>
                {this.state.popup ?
                    <div className="popup">
                        <div className="popup-body">
                            <div className="popup-title">
                                {this.state.popupTitle}
                            </div>
                            <form onSubmit={this.handleSubmit}>
                                <input type="text" className="form-control" style={{display:'none'}}/>
                                <input type="hidden" value={this.state.poolId} name="poolId"/>
                                <div className="row">
                                    <div className="col-sm-4">
                                        <div className="form-group">
                                            <label>Custom Pool Name</label>
                                            <input type="text"
                                                   className="form-control"
                                                   name="poolName"
                                                   value={this.state.poolName}
                                                   onChange={this.handleInputChange}
                                            />
                                        </div>
                                    </div>
                                    <div className="col-sm-4">
                                        <div className="form-group">
                                            <label>Coin</label>
                                            <select className="form-control"
                                                    name="poolCoin"
                                                    value={this.state.poolCoin}
                                                    onChange={this.handleInputChange}
                                            >
                                                {coinOptions}
                                            </select>
                                        </div>
                                    </div>
                                    <div className="col-sm-4">
                                        <div className="form-group">
                                            <label>Algorithm</label>
                                            <select className="form-control"
                                                    name="poolAlgo"
                                                    value={this.state.poolAlgo}
                                                    onChange={this.handleInputChange}
                                            >
                                                {algorithmOptions}
                                            </select>
                                        </div>
                                    </div>
                                </div>

                                <div className="row">
                                    <div className="col-sm-9">
                                        <div className="form-group">
                                            <label>Stratum Hostname/IP</label>
                                            <div className="input-group">
                                                <div className="input-group-addon">stratum+tcp://</div>
                                                <input type="text"
                                                       className="form-control"
                                                       name="poolUrl"
                                                       value={this.state.poolUrl}
                                                       onChange={this.handleInputChange}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-sm-3">
                                        <div className="form-group">
                                            <label>Port</label>
                                            <input type="text"
                                                   className="form-control"
                                                   name="poolPort"
                                                   value={this.state.poolPort}
                                                   onChange={this.handleInputChange}
                                            />
                                        </div>
                                    </div>
                                </div>

                                <div className="row">
                                    <div className="col-sm-8">
                                        <div className="form-group">
                                            <label>Username</label>
                                            <input type="text"
                                                   className="form-control"
                                                   name="poolUsername"
                                                   value={this.state.poolUsername}
                                                   onChange={this.handleInputChange}
                                            />
                                        </div>
                                    </div>
                                    <div className="col-sm-4">
                                        <div className="form-group">
                                            <label>Password</label>
                                            <input type="text"
                                                   className="form-control"
                                                   name="poolPassword"
                                                   value={this.state.poolPassword}
                                                   onChange={this.handleInputChange}
                                            />
                                        </div>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-sm-9">
                                        <div className="form-group">
                                            <label>Link</label>
                                            <input type="text"
                                                   className="form-control"
                                                   name="poolLink"
                                                   value={this.state.poolLink}
                                                   onChange={this.handleInputChange}
                                            />
                                        </div>
                                    </div>
                                    <div className="col-sm-3">
                                        <div className="form-group">
                                            <label>Is Active</label>
                                            <select className="form-control"
                                                    name="poolActive"
                                                    value={this.state.poolActive}
                                                    onChange={this.handleInputChange}
                                            >
                                                <option value="0">Inactive</option>
                                                <option value="1">Active</option>
                                            </select>
                                        </div>
                                    </div>
                                </div>
                                <hr/>
                                <div className="row">
                                    <div className="col-sm-6">
                                        <button type="button"
                                                className="btn btn-default"
                                                onClick={this.verifyPoolPopup.bind(this)}
                                        >
                                            Test Pool
                                        </button>
                                    </div>
                                    <div className="col-sm-6 text-right">
                                        <button type="submit" className="btn btn-warning">
                                            <i className="fa fa-save"></i> Save Pool
                                        </button>
                                    </div>
                                </div>
                            </form>
                        </div>
                        <div className="popup-background" onClick={this.closePopup.bind(this)}></div>
                    </div>
                    :
                    null
                }
            </div>
        )

    }
}

export default Pools;



