import {autorun, observable} from 'mobx';
import openSocket from 'socket.io-client';
import Cookies from 'universal-cookie';
import {cookieLogin} from './tools';
const socketURL = process.env.NODE_ENV === 'development'? 'http://localhost:8000':'/';
const socket = openSocket(socketURL);

class Store {
    // connection
    @observable connected = false;

    // coin info
    @observable coins = null;

    // proxy
    @observable proxies = null;
    @observable allProxies = null;
    @observable algos = null;

    //user stuff
    @observable pools = null;

    //auth
    @observable user_key = null;
    @observable username = null;
    @observable isAuthenticating = false;
}

let store = window.store = new Store();
window.socket = socket;

export default store;

autorun(()=>{
});

// store updater
socket.on('connect', function() {
    console.log(`Connected to ${socketURL}`);
    store.connected = true;
    // try to login with cookies if we have them
    cookieLogin(store);
});

socket.on('coins', (coins) => {
    store.coins = coins;
});

socket.on('pools', (pools) => {
    store.pools = pools;
});

socket.on('algos', (algos) => {
    store.algos = algos;
});

socket.on('credentials', (data) => {
    console.log(`Did login ${data.isAuth}`);
    if(data.isAuth){
        // authenticated
        const cookies = new Cookies();
        // set cookies for 2 days
        cookies.set('username', data.username, {path: '/', maxAge: 60*60*24*2});
        cookies.set('user_key', data.user_key, {path: '/', maxAge: 60*60*24*2});
        store.user_key = data.user_key;
        store.username = data.username;
    }
});

socket.on('disconnect', function () {
    console.log('Disconnected');
    store.proxies = null;
    store.connected = false;
});

socket.on('proxies', function (data) {
    store.proxies = data;
});


