import React, {Component} from 'react';
import {observer} from 'mobx-react';

class Row extends Component {
    render(){
        return(
            <tr>
                <td>{this.props.algo}</td>
                <td>{this.props.port}</td>
            </tr>
        )
    }
}
@observer
class AllProxies extends Component {
    render() {
        const proxies = this.props.store.algos;
        let rows = [];
        if(proxies){
            for(let i = 0; i < proxies.length; i++){
                rows.push(<Row algo={proxies[i].name} port={proxies[i].port} key={i}/>)
            }
        }
        return(
            <div className="panel panel-default">
                <div className="panel-heading">
                    All Proxies
                </div>
                <div className="panel-body">
                    <table className="compact" style={{marginBottom:'5px'}}>
                        <thead>
                        <tr>
                            <th>Algo</th>
                            <th>Port</th>
                        </tr>
                        </thead>
                        <tbody>
                        {rows}
                        </tbody>
                    </table>
                </div>
            </div>
        )

    }
}

export default AllProxies;



