import React, {Component} from 'react';
import {observer} from 'mobx-react';
import logo from '../assets/img/logo.png';
import goaway from '../assets/img/goaway.gif';
import {passwordLogin} from '../tools';
const Loading = require('react-loading-animation');

@observer
class Auth extends Component {
    constructor(props) {
        super(props);
        this.state = {
            username: '',
            password: '',
            fuckoff: false
        };

        this.handleInputChange = this.handleInputChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleInputChange(event) {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        this.setState({
            [name]: value
        });
    }

    handleSubmit(event) {
        event.preventDefault();
        passwordLogin(this.props.store, this.state.username, this.state.password, (isSuccess)=>{
            // a success with automatically log in
            if(!isSuccess){
                this.setState({
                    fuckoff: true
                })
            }
        });
    }

    render() {
        return (
            <div className="auth">
                <div className="auth-box">
                    <div className="login-box login-sidebar">
                        <div className="white-box">
                            <form className="form-horizontal form-material"
                                  id="loginform"
                                  onSubmit={this.handleSubmit}
                            >
                                {/*<a href="javascript:void(0)" className="text-center db"><img src={icon} alt="Home" style={{*/}
                                {/*width: '150px',*/}
                                {/*marginBottom: '20px'*/}
                                {/*}}/>*/}
                                <img className="logo" src={logo}/>
                                {this.props.store.isAuthenticating ? null :
                                    <div className="infobox">
                                        If you don't have a user name and password, then you dont belong here.
                                        So go away. Fuck off. Get the fuck out. Leave. Members only. No, you cannot
                                        become a member. Are you fucking crazy? Seriously leave.
                                    </div>
                                }
                                {this.state.fuckoff ?
                                    <img src={goaway} style={{borderRadius: '50px'}} className="logo"/>
                                    :
                                    this.props.store.isAuthenticating ?
                                        <div className="text-center">
                                            <h4>Authenticating</h4>
                                            <Loading width="60px" height="60px" strokeWidth="2px"/>
                                        </div>
                                        :
                                        <div>
                                            <div className="form-group m-t-40">
                                                <div className="col-xs-12">
                                                    <input className="form-control"
                                                           type="text"
                                                           required=""
                                                           name="username"
                                                           placeholder="Username"
                                                           onChange={this.handleInputChange}
                                                           value={this.state.username}
                                                    />
                                                </div>
                                            </div>
                                            <div className="form-group">
                                                <div className="col-xs-12">
                                                    <input className="form-control"
                                                           type="password"
                                                           required=""
                                                           name="password"
                                                           placeholder="Password"
                                                           onChange={this.handleInputChange}
                                                           value={this.state.password}
                                                    />
                                                </div>
                                            </div>
                                            <div className="form-group text-center m-t-20">
                                                <div className="col-xs-12">
                                                    <button
                                                        className="btn btn-warning btn-lg btn-block text-uppercase btn-login"
                                                        type="submit">
                                                        Log In
                                                    </button>
                                                </div>
                                            </div>
                                        </div>

                                }
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
export default Auth;


