import React, {Component} from 'react';
import {observer} from 'mobx-react';
import numeral from 'numeral';
import {getExchangeLink} from '../helpers';

@observer
class Row extends Component {
    render(){
        const data = this.props.data;
        const getPercent = (decimal)=>{

            let isGain =  decimal > 0;
            const color = isGain ? '#236523': '#d04b34';
            const style = {color:color};
            return(<span style={style}>{numeral(decimal*100).format('0,0.00')}</span>);
        };
        const changeBox = (value)=>{
            let background;
            if(value > 0){
                background = '#236523';
            } else {
                background = '#d04b34';
            }
            if(value === 0){
                background = '#666666';
            }
            const style = {background:background};
            return(
                <span style={style} className="change-box">
                    {numeral(value).format('0,0.00')}%
                </span>
            );

        };
        const formatToNH = (hash)=>{
            let newSpeed = hash;
            switch(data.NHspeedText){
                case 'H': newSpeed = hash;break;
                case 'kH': newSpeed = hash / 1000;break;
                case 'KH': newSpeed = hash / 1000;break;
                case 'MH': newSpeed = hash / 1000000;break;
                case 'GH': newSpeed = hash / 1000000000;break;
                case 'TH': newSpeed = hash / 1000000000000;break;
                case 'PH': newSpeed = hash / 1000000000000000;break;
            }
            return(<span>{numeral(newSpeed).format('0,0.0000')} {data.NHspeedText}</span>)
        };
        const dualRow = {padding: '0 8px'};
        const img = `/img/${data.ticker}.png`;
        const exchangeImg = `/img/${data.exchange}.png`;
        const sliderClass = 'toggle active';
        return(
            <tr>
                <td className="imgcell"><img src={img}/></td>
                <td>{data.ticker}</td>
                <td className="hide-7">{data.name}</td>
                <td className="hide-8">{data.algo}</td>
                <td>
                    {numeral(data.price).format('0,0.00000000')}
                    {data.change24 ? <br/> : null}
                    {data.change24 ? changeBox(data.change24) : null}
                </td>
                <td className="hide-2">
                    {numeral(data.currentBlock).format('0,0')}
                </td>
                <td className="hide-9">
                    {formatToNH(data.networkHashrate)}
                </td>
                <td className="hide-3">
                    {numeral(data.difficulty).format('0,0.00')}
                </td>
                <td className="hide-1">
                    <a href={getExchangeLink(data.exchange, data.ticker)} target="_blank">
                    <img style={{height:'16px'}} src={exchangeImg}/>
                    </a>
                </td>
                <td style={dualRow}>
                    {numeral(data.NHpriceLowEU).format('0.0000')} {data.NHspeedText}<br/>
                    {numeral(data.NHpriceMedEU).format('0.0000')} {data.NHspeedText}<br/>
                    {numeral(data.NHpriceHighEU).format('0.0000')} {data.NHspeedText}
                </td>
                <td style={dualRow}>
                    {numeral(data.NHpriceLowUSA).format('0.0000')} {data.NHspeedText}<br/>
                    {numeral(data.NHpriceMedUSA).format('0.0000')} {data.NHspeedText}<br/>
                    {numeral(data.NHpriceHighUSA).format('0.0000')} {data.NHspeedText}
                </td>
                <td style={dualRow}>
                    {getPercent(data.ROIhighEU)}%<br/>
                    {getPercent(data.ROImedEU)}%<br/>
                    {getPercent(data.ROIlowEU)}%
                </td>
                <td style={dualRow}>
                    {getPercent(data.ROIhighUSA)}%<br/>
                    {getPercent(data.ROImedUSA)}%<br/>
                    {getPercent(data.ROIlowUSA)}%
                </td>
                <td className="toggle-col">
                    <div className={sliderClass}>
                        <div className="ball"></div>
                    </div>
                </td>
            </tr>
        );
    }
}

@observer
class ProfitabilityTable extends Component {
    render() {
        let rows = [];
        let coins = this.props.store.coins;
        let coinArr = [];
        function sort(a,b) {
            if (a.ROIhighEU < b.ROIhighEU)
                return 1;
            if (a.ROIhighEU > b.ROIhighEU)
                return -1;
            return 0;
        }
        for(let coin in coins){
            coinArr.push(coins[coin]);
        }
        coinArr = coinArr.sort(sort);
        for(let i = 0; i < coinArr.length; i++){
            rows.push(<Row data={coinArr[i]} key={i}/>)
        }

        return (
            <table className="table table-striped compact" style={{marginTop: '-20px'}}>
                <thead>
                <tr>
                    <th className="imgcell"></th>
                    <th>Ticker</th>
                    <th className="hide-7">Name</th>
                    <th className="hide-8">Algo</th>
                    <th>Price</th>
                    <th className="hide-2">Blocks</th>
                    <th className="hide-9">Network H/s</th>
                    <th className="hide-3">Difficulty</th>
                    <th className="hide-1">Exchange</th>
                    <th>NH EU</th>
                    <th>NH USA</th>
                    <th>ROI EU</th>
                    <th>ROI USA</th>
                    <th className="slider-col">Active</th>
                </tr>
                </thead>
                <tbody>
                {rows}
                </tbody>
            </table>
        )

    }
}

export default ProfitabilityTable;



