import React, {Component} from 'react';
import {observer} from 'mobx-react';

import logo from './assets/img/logo.png'
import Orders from "./pages/Orders";
import Home from './pages/Home';
import {clearAuth} from './tools';
import { TransitionGroup, CSSTransition } from "react-transition-group";
import {
    BrowserRouter as Router,
    Route,
    NavLink,
    Switch,
    Link
} from 'react-router-dom'
import Wallet from "./pages/Wallet";
import Proxy from "./pages/Proxy";
import Config from "./pages/Config";
import Pools from "./pages/Pools";
import Coins from "./pages/Coins";

class NavItem extends Component {
    render() {
        const {to, exact, strict, activeClassName, className, activeStyle, style, isActive: getIsActive, ...rest} = this.props;
        return (
            <Route
                path={typeof to === 'object' ? to.pathname : to}
                exact={exact}
                strict={strict}
                children={({location, match}) => {
                    const isActive = !!(getIsActive ? getIsActive(match, location) : match)

                    return (
                        <li
                            className={isActive ? [activeClassName, className].join(' ') : className}
                            style={isActive ? {...style, ...activeStyle} : style}>
                            <Link
                                to={to}
                                {...rest}
                            />
                        </li>
                    )
                }}
            />
        );
    }
}

@observer
class App extends Component {
    logout(event) {
        event.preventDefault();
        clearAuth(this.props.store);
    }

    render() {
        // check to see if access cookie exists.
        const duration = 300;
        const defaultStyle = {
            transition: `opacity ${duration}ms ease-in-out`,
            opacity: 0,
        };

        const transitionStyles = {
            entering: { opacity: 0 },
            entered:  { opacity: 1 },
        };
        return (
            <Router>
                <div className="relative">
                    <nav className="navbar navbar-inverse navbar-fixed-top">
                        <div className="container-fluid">
                            <div className="navbar-header">
                                <button type="button" className="navbar-toggle collapsed">
                                    <span className="sr-only">Toggle navigation</span>
                                    <span className="icon-bar"></span>
                                    <span className="icon-bar"></span>
                                    <span className="icon-bar"></span>
                                </button>
                                <a className="navbar-brand" href="#">
                                    <img src={logo}/>
                                </a>
                            </div>
                            <div id="navbar" className="collapse navbar-collapse">
                                <ul className="nav navbar-nav">
                                    <NavItem activeClassName='active' exact={true} strict to="/"><i
                                        className="fa fa-dashboard"></i> Main</NavItem>
                                    <NavItem activeClassName='active' to="/coins"><i
                                        className="fa fa-btc"></i> Coins</NavItem>
                                    <NavItem activeClassName='active' to="/wallet"><i
                                        className="fa fa-credit-card-alt"></i> Wallets</NavItem>
                                    <NavItem activeClassName='active' to="/orders"><i
                                        className="fa fa-shopping-cart"></i> Orders</NavItem>
                                    <NavItem activeClassName='active' to="/proxy"><i className="fa fa-retweet"></i>
                                        Proxy</NavItem>
                                    <NavItem activeClassName='active' to="/pools"><i className="fa fa-sitemap"></i>
                                        Pools</NavItem>
                                    <NavItem activeClassName='active' to="/config"><i className="fa fa-gears"></i>
                                        Config</NavItem>
                                </ul>
                                <ul className="nav navbar-nav navbar-right">
                                    {/*<li className="active"><a href="#">Home</a></li>*/}
                                    {/*<li><a href="#about">About</a></li>*/}
                                    <li><a title="Logout" onClick={this.logout.bind(this)}><i
                                        className="fa fa-sign-out"></i> Logout</a></li>
                                </ul>
                            </div>
                        </div>
                    </nav>
                    <Route
                        style={{position:'relative'}}
                        render={({ location }) => (
                            <TransitionGroup>
                                <CSSTransition key={location.key} classNames="fade" timeout={700}>
                                    <Switch location={location}>
                                        <Route exact path="/" render={props => (<Home {...props}{...this.props} />)}/>
                                        <Route path="/coins" render={props => (<Coins {...props}{...this.props} />)}/>
                                        <Route path="/wallet" render={props => (<Wallet {...props}{...this.props} />)}/>
                                        <Route path="/orders" render={props => (<Orders {...props}{...this.props} />)}/>
                                        <Route path="/proxy" render={props => (<Proxy {...props}{...this.props} />)}/>
                                        <Route path="/pools" render={props => (<Pools {...props}{...this.props} />)}/>
                                        <Route path="/config" render={props => (<Config {...props}{...this.props} />)}/>
                                    </Switch>
                                </CSSTransition>
                            </TransitionGroup>
                        )}
                    />
                </div>
            </Router>
        )

    }
}

export default App;



