import React, {Component} from 'react';
import {observer} from 'mobx-react';
import ActiveProxies from "../components/proxy/ActiveProxies";
import AllProxies from "../components/proxy/AllProxies";
import PageTitle from "../components/PageTitle";

@observer
class Proxy extends Component {
    render() {
        // check to see if access cookie exists.
        return (
            <div className="container-fluid content">
                <PageTitle text="Stratum Proxy" store={this.props.store}/>
                <div className="row">
                    <div className="col-md-9 col-sm-8">

                    </div>
                    <div className="col-md-3 col-sm-4">
                        <ActiveProxies store={this.props.store}/>
                        <AllProxies store={this.props.store}/>
                    </div>
                </div>

            </div>
        )

    }
}

export default Proxy;



