import React, {Component} from 'react';
import {observer} from 'mobx-react';

class Row extends Component {
    render(){
        return(
            <tr>
                <td>{this.props.algo}</td>
                <td>{this.props.numMiners}</td>
            </tr>
        )
    }
}
@observer
class ActiveProxies extends Component {
    render() {
        const proxies = this.props.store.proxies;
        let activeProxies = {};
        let numActiveProxies = 0;
        let rows = [];
        if(proxies){
            for(let i = 0; i < proxies.length; i++){
                if(proxies[i].proxy.algo in activeProxies){
                    activeProxies[proxies[i].proxy.algo]++;
                } else {
                    activeProxies[proxies[i].proxy.algo] = 1;
                }
                numActiveProxies++;
            }
        }
        if(proxies){
            for(let algo in activeProxies){
                rows.push(<Row algo={algo} numMiners={activeProxies[algo]} key={algo}/>)
            }
        }
        return(
            <div className="panel panel-default">
                <div className="panel-heading">
                    Active Proxies ({numActiveProxies})
                </div>
                <div className="panel-body">
                    <table className="compact" style={{marginBottom:'5px'}}>
                        <thead>
                        <tr>
                            <th>Algo</th>
                            <th>Miners</th>
                        </tr>
                        </thead>
                        <tbody>
                        {rows}
                        </tbody>
                    </table>
                </div>
            </div>
        )

    }
}

export default ActiveProxies;



