export function getExchangeLink(exchange, ticker){
    switch(exchange){
        case 'southxchange': return `https://www.southxchange.com/Market/Book/${ticker}/BTC`;break;
        case 'stocksexchange':return `https://stocks.exchange/trade/${ticker}/BTC`; break;
        default: return `#`;
    }
}


